
.App {
  padding: 1rem;
  color: white;
  background-color: black;
  text-align: center;
  height: 100vh;
  box-sizing: border-box;
  overflow: scroll;
}

.sourceLink{
  color: white;
  background-color: rgba(0,0,0,0.5);
  padding: 15px;
  bottom: 0;
  box-sizing: border-box;
  display: inline-block;
  margin: 2%;
}

.backgroundFont{
  font-family: 'Gloria Hallelujah', cursive;
  color: white;
  position: fixed;
  right: 140px;
  bottom: 120px;
  font-size: 4rem;
  transform: rotate(-35deg);
  opacity: 0.7;
}